<template>
	<div id="memberProfile">
		<public-header title="会员资料"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell title="会员uid" :value="user.uid" />
				<van-cell title="会员头像" class="flex-lr" center is-link>
					<template slog="default">
						<div class="avatar">
							<img :src="user.avatar"/>
							<input class="upload-input" type="file" accept="image/*" @change="onUploadImage($event)">
						</div>
					</template>
				</van-cell>
				<van-cell title="手机号" :value="user.mobile ? user.mobile : '绑定手机号'" :to="util.getUrl({path: '/pages/member/bind?mobile='  + user.mobile})" is-link/>
				<van-cell title="用户名" :value="user.realname" is-link :to="util.getUrl({path: '/pages/member/profileUsername?username=' + user.realname})"/>
				<van-cell title="账号密码" value="修改" is-link :to="util.getUrl({path: '/pages/member/profilePassword'})"/>
				<van-cell title="注销账号" value="注销后无法恢复, 请谨慎操作" class="delete" is-link :to="util.getUrl({path: '/pages/member/delete'})" v-if="util.isGlala()"/>
			</van-cell-group>
			<van-cell-group class="margin-10-t" v-if="user.show_language == 1">
				<van-cell title="语言" is-link :to="util.getUrl({path: '/core/pages/member/language', query: {}})"/>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="服务协议/隐私政策" is-link :to="util.getUrl({path: '/pages/member/agreement', query: {type: 'tips'}})"/>
			</van-cell-group>
			<div class="submit">
				<van-button v-if="!util.isWeixin()" type="danger" @click="onLogout" size="normal" block="block">退出登录</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import { Dialog } from 'vant';
	import PublicHeader from '@/components/header'
	export default {
		data() {
			return {
				block: true,
				user: {},
				showPreLoading: true
			}
		},
		components: {
			PublicHeader,
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'wmall/member/profile'
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						return;
					}
					this.user = result.message;
				})
			},

			onLogout() {
				Dialog.confirm({
					title: '温馨提示',
					message: '确定退出登录吗？'
				}).then(() => {
					this.util.removeStorage('itoken');
					this.util.delCookie('itoken');
					this.$router.replace(this.util.getUrl({path: '/pages/auth/login'}));
				})
			},

			onUploadImage(e) {
				var that = this;
				that.util.image({
					obj: e,
					success: function(obj, result) {
						if(result.url && result.filename) {
							that.util.request({
								url: 'wmall/member/profile/avatar',
								data: {
									avatar: result.filename,
								},
								method: 'POST',
							}).then((res) => {
								res = res.data.message;
								that.util.$toast(res.message);
								if(res.errno) {
									return;
								}
								that.$set(that.user, 'avatar', result.url)
							})
						}
					},
					options: {channel: 'h5'}
				});
			},
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#memberProfile .content{
		padding-top: 10px;
	}
	#memberProfile .content .van-cell__value span{
		color:#666;
	}
	#memberProfile .content .submit{
		margin-top: 20px;
		padding: 0 15px;
	}
	#memberProfile .avatar{
		position: relative;
		display: inline-block;
		width: 60px;
		height: 60px;
		border-radius: 50%;
		padding: 2px;
		font-size: 0px;
	}
	#memberProfile .avatar img{
		width: 100%;
		height: 100%;
		border-radius: 50%;
	}
	#memberProfile .avatar .upload-input{
		position: absolute;
		display: block;
		opacity: 0;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
	}
	#memberProfile .delete .van-cell__value{
		min-width: 60%;
		white-space: nowrap;
	}
</style>



